.filter-bar{align-items:center;display:flex;flex-direction:column;margin-bottom:10px}.input-group{display:flex;flex-wrap:wrap;justify-content:center;margin-bottom:5px}.filter-date,.search-input{margin:5px}.search-input{max-width:100%;width:15rem}.filter-date,.search-input{border:1px solid #ccc;border-radius:5px;font-family:Helvetica Neue,Helvetica,Arial,sans-serif;font-size:1rem;padding:10px}.filter-date{color:#848484}.button-group{display:flex;justify-content:center}.reset-button,.search-button{background-color:#92accd;border:1px solid #92accd;border-radius:5px;color:#fff;cursor:pointer;font-family:Helvetica Neue,Helvetica,Arial,sans-serif;font-size:1rem;margin:5px;padding:10px 20px;transition:background-color .3s ease,transform .2s ease}.search-button:hover{background-color:#3b74ad;transform:scale(1.05)}.reset-button{background-color:#f39a70}.reset-button:hover{background-color:#ce7044;transform:scale(1.05)}@media (max-width:700px){.filter-date,.search-input{border-radius:5px;width:100%}.reset-button,.search-button{border-radius:5px;margin-top:5px;width:100%}}